html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

a {
  color: rgb(151, 141, 122);
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img, embed, object, video {
  max-width: 100%;
}

.App {
  background-color: #f0eff0;
}

.App-header {
  background-color: #0a2eb3;
  font-size: calc(10px + 1vmin);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 1vh 0;
}

.App-logo {
  height: 100px;
  width: 100px;
}

.App-container {
  color: white;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
}

.App-hero {
  height: 300px;
  margin: 0 0 1vh 0;
}
.ImageList {
  display: flex;
  overflow-x: auto;
  margin: 0 15px 30px;
	padding: 10px 10px 15px;
	text-align: center;
	text-decoration: none;
}

.ImageList::-webkit-scrollbar {
  display: none;
}
.Image {
  height: 500px;
  width: 500px;
  margin-right: 15px;
}
.Boxes {
  color: brown;
}
